import axios from "axios";
import React, { Component } from "react";
import { API } from "../../utils/api";
import Loading from "../Loading/Loading";

interface ClaimEvaluationHistoryProps {
  claim_level: number;
}
interface ClaimEvaluationHistoryState {
  loading: boolean;
  evaluators: EvaluatorInterface[] | null;
}

interface EvaluatorInterface {
  email: string;
  evaluated: number;
  name: string;
}

class ClaimEvaluationHistory extends Component<
  ClaimEvaluationHistoryProps,
  ClaimEvaluationHistoryState
> {
  constructor(props: ClaimEvaluationHistoryProps) {
    super(props);

    this.state = {
      loading: true,
      evaluators: null,
    };
  }
  GetEvaluationHistory = async () => {
    this.setState({
      loading: true,
    });
    try {
      const res = await axios.post<EvaluatorInterface[]>(
        `${API.CLAIMING}/user/evaluator/${this.props.claim_level}`,
        {
          period_id: "62a5ea76b038313901018796",
        }
      );
      if (res.data) {
        this.setState({
          evaluators: res.data,
          loading: false,
        });
      }
    } catch (error: any) {
      this.setState({ loading: false });
      console.log("Result: ", { ...error });
    }
  };
  componentDidMount = () => {
    this.GetEvaluationHistory();
  };
  render() {
    if (this.state.loading === true || this.state.evaluators === null) {
      return (
        <div className="bg-white rounded p-5 mt-5">
          <Loading />
          <div className="-mt-5 ml-5">Loading data, please wait...</div>
        </div>
      );
    }
    return (
      <div>
        <div>
          <table className="w-full">
            <thead className="bg-blue-100">
              <tr>
                <th className="px-3 py-2 text-center">#</th>
                <th className="px-3 py-2 text-left">Names</th>
                <th className="px-3 py-2 text-left">Email</th>
                <th className="px-3 py-2 text-center">Counts</th>
              </tr>
            </thead>
            <tbody>
              {this.state.evaluators.length === 0 ? (
                <tr>
                  <td
                    colSpan={4}
                    className="bg-gray-200 p-5 text-center font-light text-3xl"
                  >
                    No result found!
                  </td>
                </tr>
              ) : (
                this.state.evaluators
                  .sort((a, b) =>
                    a.evaluated < b.evaluated
                      ? 1
                      : b.evaluated < a.evaluated
                      ? -1
                      : 0
                  )
                  .map((item, i) => (
                    <tr
                      key={i + 1}
                      className={`${i % 2 !== 0 ? "bg-gray-100" : ""}`}
                    >
                      <td className="px-3 py-1 text-center">{i + 1}</td>
                      <td className="px-3 py-1 text-left">{item.name}</td>
                      <td className="px-3 py-1 text-left">{item.email}</td>
                      <td className="px-3 py-1 text-center">
                        {item.evaluated}
                      </td>
                    </tr>
                  ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default ClaimEvaluationHistory;
